<template>
  <div class="tags">
    <span class="switch-aside" @click="openAside">
      <i :class="isCollapseVuex"></i>
      <!-- <menu-fold theme="outline" size="24" fill="#333" /> -->
      <!-- <i class="iconfont icon-biangeng"></i> -->
    </span>
    <!-- <span class="switch-aside"><i class="el-icon-menu"></i></span> -->
    <el-scrollbar>
      <el-tag
        :key="tag.itemName"
        size="medium"
        v-for="(tag, index) in tags"
        :disable-transitions="false"
        :effect="
          $route.name === tag.routeName && tag.itemName === currentTag.itemName
            ? 'dark'
            : 'plain'
        "
        :closable="tag.icon !== 'home'"
        type="success"
        @click="changeMenu(tag)"
        @close="handleClose(tag, index)"
      >
        {{ tag.itemName }}
      </el-tag>
    </el-scrollbar>
    <!-- <span class="switch-aside"><i class="el-icon-menu"></i></span> -->
  </div>
</template>

<script>
// import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {};
  },
  computed: {
    // 标签页列表
    tags() {
      return this.$store.state.NavigationPage.compTag.tags;
    },
    // 当前选中的标签页
    currentTag() {
      return this.$store.state.NavigationPage.compTag.currentTag;
    },

    // 选中的二级菜单的下标
    itemIndex() {
      return this.$store.state.ItemDetails.itemIndex;
    },
    // 切换关闭图标
    isCollapseVuex() {
      return this.$store.state.projectManage.open
        ? "el-icon-s-unfold"
        : "el-icon-s-fold";
    },
  },
  methods: {
    // 选中标签页
    changeMenu(tag) {
      console.log(tag);
      if (tag.routeName !== this.$route.name) {
        this.$store.commit("ItemDetails/currentType", tag.routeName);
        this.$router.push({ name: tag.routeName });
      } else {
        this.$store.commit("NavigationPage/setCompTag", {
          key: "currentTag",
          value: tag,
        });
        this.$store.commit("ItemDetails/currentDate", {
          itemDate: tag.itemName,
          itemIndex: tag.itemName[this.itemIndex],
        });
      }
    },
    // 关闭标签页
    handleClose(tag, index) {
      let length = this.tags.length - 1;
      this.$store.commit("NavigationPage/delCompTag", tag);
      // 如果关闭的标签不是当前路由的话，就不跳转
      if (tag.routeName !== this.$route.name) {
        return;
      }
      // 关闭的标签是最右边的话，往左边跳转一个
      if (index === length) {
        this.$router.push({ name: this.tags[index - 1].routeName });
      } else {
        // 否则往右边跳转
        this.$router.push({ name: this.tags[index].routeName });
      }
      console.log(tag);
    },
    // 控制侧边栏的收缩
    openAside() {
      this.$store.commit("projectManage/setOpen");
    },
  },
  mounted() {},
};
</script>

<style lang="scss" scoped>
.tags {
  display: flex;
  flex-wrap: nowrap;
  overflow: auto;
  // margin-right: 20px;
  height: 30px;
  width: 100%;
  .el-scrollbar {
    height: 30px;
  }
  .switch-aside {
    width: 40px;
    height: 30px;
    font-size: 24px;
    line-height: 30px;
    margin-left: 20px;
  }
  .el-tag {
    margin-left: 15px;
  }
}
</style>
<style lang="scss">
.tags {
  .el-scrollbar__wrap {
    overflow: scroll;
    width: calc(100% + 20px);
    height: calc(100% + 20px);
    white-space: nowrap;
  }
}
</style>
