<template>
  <!-- default-active="1" -->
  <el-menu
    id="aside-menu"
    class="el-menu-vertical-demo"
    @select="handleSelect"
    :collapse="isCollapseVuex"
    text-color="#fff"
    active-text-color="#ffd04b"
  >
    <el-submenu
      :index="group.index"
      v-for="(group, index) in navArray"
      :key="index"
    >
      <template slot="title">
        <i :class="group.groupIcon"></i>
        <span slot="title">{{ group.groupName }}</span>
      </template>
      <el-menu-item
        :index="children.index"
        v-for="(children, childrenIndex) in group.children"
        :key="index + '-' + childrenIndex"
      >
        <template slot="title">
          <i class="el-icon-location"></i>
          <span slot="title">{{ children.title }}</span>
        </template>
      </el-menu-item>
    </el-submenu>
  </el-menu>
</template>

<script>
export default {
  data() {
    return {
      // isCollapse: true,
      navArray: [
        {
          groupName: "算法方案管理",
          groupIcon: "el-icon-menu",
          index: "1",
          parentName: "projectManage",
          children: [
            {
              title: "图片分类",
              icon: "el-icon-location",
              index: "1-1",
            },
            {
              title: "异常检测",
              icon: "el-icon-location",
              index: "1-2",
            },
            {
              title: "数据预测",
              icon: "el-icon-location",
              index: "1-3",
            },
            {
              title: "目标检测",
              icon: "el-icon-location",
              index: "1-4",
            },
          ],
        },
        {
          groupName: "数据集管理",
          groupIcon: "el-icon-document",
          parentName: "dataManage",
          index: "2",
          children: [
            {
              title: "图片",
              icon: "el-icon-location",
              index: "2-1",
              targetRoute: "image",
            },
            {
              title: "文本",
              icon: "el-icon-location",
              index: "2-2",

              targetRoute: "text",
            },
            {
              title: "视频",
              icon: "el-icon-location",
              index: "2-3",

              targetRoute: "video",
            },
            {
              title: "音频",
              icon: "el-icon-location",
              index: "2-4",

              targetRoute: "audio",
            },
            {
              title: "遥感",
              icon: "el-icon-location",
              index: "2-5",

              targetRoute: "remote_sensing",
            },
          ],
        },
      ],
      selectType: "",
    };
  },
  computed: {
    currentType() {
      return this.$store.state.ItemDetails.currentType;
    },
    isCollapseVuex() {
      return this.$store.state.projectManage.open;
    },
  },
  // watch: {
  //   currentType: {
  //     handler(newValue) {
  //       // this.selectType = newValue;
  //       this.$store.commit("ItemDetails/currentType", newValue);
  //     },
  //   },
  // },
  methods: {
    // 点击路由跳转
    handleSelect(key, keyPath) {
      console.log(this.$route);
      // console.log(this.$router);
      console.log(keyPath);
      let parentPath = this.navArray[parseInt(key) - 1];
      console.log(parentPath);
      let childIndex = keyPath[1].split("-")[1];
      let child = parentPath.children[childIndex - 1];
      if (parentPath.parentName === "projectManage") {
        if (this.$route.name !== "ProjectManage") {
          this.$router.push({
            name: "ProjectManage",
          });
        }
        this.$store.commit("projectManage/setAsideLeft", {
          key: "projectType",
          value: {
            name: child.title,
            type: child.title,
          },
        });
      } else if (parentPath.parentName === "dataManage") {
        console.log(child.targetRoute);
        this.$store.commit("ItemDetails/currentType", child.targetRoute);
        this.$router.push({
          name: child.targetRoute,
        });
      }
    },
    // 选中第一个选项提交vuex
    selectProjectType() {
      let type = this.navArray[0].children[0].title;
      this.$store.commit("projectManage/setAsideLeft", {
        key: "projectType",
        value: { name: type, type: type },
      });
    },
  },
  created() {
    // 选中导航栏第一个选项提交vuex
    this.selectProjectType();
  },
};
</script>

<style lang="scss" scoped>
.el-menu {
  height: 100%;
  width: 100%;
}
</style>
<style lang="scss">
.el-menu {
  background: linear-gradient(
    180deg,
    rgba(76, 83, 127, 1) 44%,
    rgba(28, 30, 45, 1) 99%
  );

  .el-menu-item:hover {
    background: #1c88cf !important;
    color: #fff !important;
  }
  .el-submenu__title:hover {
    background: #1c88cf !important;
    color: #fff !important;
  }
  // 选中时的背景色和字体色
  // .el-menu-item.is-active {
  //   background: #1c88cf !important;
  //   color: #fff !important;
  // }
  // .el-submenu__title.is-active {
  //   background: #1c88cf !important;
  //   color: #fff !important;
  // }
}
</style>
